body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.badgeNew{
  /* border: solid 1px yellowgreen; */
  background-image: url(/bazarMonteVerde/static/media/bg.e47d4a5a.png)
}


.navbar{
    width: 100%;
    height: 5em;
    /* border: solid 1px red; */
    display: flex;
    align-items: center;
    background-color: #161c35;
}

.logoNavbar{
    width: 50px;
    height: 55px;
    margin-left: 2em;
}
.masterBadge{
    /* border: solid 1px orangered; */
    
    display: flex;
    justify-content: center;
    font-family: myriad-pro, sans-serif;
    font-weight: 700;
    font-style: normal;
    /* background-color: #008067 */
}

.Badge{
    margin-top: 2em;
    /* border: solid 1px rgb(72, 41, 41); */
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 800px;
    height: 1200px;
    background-color: #161c35;
}

header{
    /* border: solid 1px green; */
    border-radius: inherit;
    width: 100%;
    padding: 3em 0;
    display: flex;
    justify-content: center;
    border-bottom: 5em red sold;
}

.seccionAvatar{
    /* border: solid 1px aqua; */
    width: 100%;
    height: 20%;
    display: flex;
    justify-content: center;
    border-top: 5em red sold;
    background-color: #fff;
}

.seccionAvatar img{
    padding-top: 1em;
    /* width: 200px;
    height: 200px; */

}

.datos{
    /* border: solid 1px palevioletred; */
    width: 100%;
    height: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.5em;
    background-color: #fff;
}

.datos h1 {
    text-transform: uppercase;
}

.containercode{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff;
    padding-bottom: 1em;
}
.masterContainer {
    margin-top: 2em;
    /* border: solid 1px red; */
    display: flex;
    justify-content: center;
    /* background-color: #008067; */
}

.badgeForm {
    width: 1200px;
    /* border: solid 1px red; */
    display: flex;
    justify-content: center;
}

.contenedorLogoFrom {
    /* border: solid 1px rgb(50, 122, 205); */
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 850px;
    background-color: #161c35;
}

.borderCard {
    /* border: solid 1px yellowgreen; */
    width: 40%;
    height: 850px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-image: url(/bazarMonteVerde/static/media/fondo.49bab8ab.png);
    color: #fff;
    font-family: myriad-pro, sans-serif;
    font-weight: 700;
    font-style: normal;
}

.borderCard>h1 {
    text-transform: uppercase;
    letter-spacing: 3pt;
}

.borderCard>h2 {
    text-transform: uppercase;
    color: #1fd5b9;
    letter-spacing: 2pt;
}

form {
    /* border: solid 1px #fff; */
    width: 90%;
    height: 70%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-transform: uppercase;
}

input {
    width: 95%;
    height: 3em;
    background-color: transparent;
    border: solid 1px #fff;
    margin-top: 1em;
    color: #fff;
    font-size: 15px;
}

input::-webkit-input-placeholder {
    padding-left: 0.5em;
    color: #ffffffc2;
    font-size: 15px;
}

input::-moz-placeholder {
    padding-left: 0.5em;
    color: #ffffffc2;
    font-size: 15px;
}

input::-ms-input-placeholder {
    padding-left: 0.5em;
    color: #ffffffc2;
    font-size: 15px;
}

input::placeholder {
    padding-left: 0.5em;
    color: #ffffffc2;
    font-size: 15px;
}

