select {
    background-color: transparent;
    width: 95%;
    height: 3em;
    border: solid 1px #fff;
    border-radius: 0;
    color: #ffffffc2;
    font-size: 15px;
    margin-top: 1em;
}

button {
    border: none;
    width: 96%;
    height: 3em;
    color: #fff;
    font-size: 15px;
    background-color: #1fd5b9;
    text-transform: uppercase;
    letter-spacing: 1pt;
    margin-top: 1em;
}

.metodoDePago{
    display: none;
}

.numeroDeMesa{
    display: none;
}

.reference{
    display: none;
}

.comite{
    display: none;
}

.numeroCaja{
    display: none;
}
